html {
  line-height: 1.15;
  /* 1 */
  -webkit-text-size-adjust: 100%;
  /* 2 */ }

body {
  margin: 0; }

main {
  display: block; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

hr {
  box-sizing: content-box;
  /* 1 */
  height: 0;
  /* 1 */
  overflow: visible;
  /* 2 */ }

pre {
  font-family: monospace, monospace;
  /* 1 */
  font-size: 1em;
  /* 2 */ }

a {
  background-color: transparent; }

abbr[title] {
  border-bottom: none;
  /* 1 */
  text-decoration: underline;
  /* 2 */
  text-decoration: underline dotted;
  /* 2 */ }

b,
strong {
  font-weight: bolder; }

code,
kbd,
samp {
  font-family: monospace, monospace;
  /* 1 */
  font-size: 1em;
  /* 2 */ }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sub {
  bottom: -0.25em; }

sup {
  top: -0.5em; }

img {
  border-style: none; }

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0; }

button,
input {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button; }

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0; }

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText; }

fieldset {
  padding: 0.35em 0.75em 0.625em; }

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal; }

progress {
  vertical-align: baseline; }

textarea {
  overflow: auto; }

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  /* 1 */
  padding: 0;
  /* 2 */ }

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto; }

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px; }

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit; }

details {
  display: block; }

summary {
  display: list-item; }

template {
  display: none; }

[hidden] {
  display: none; }

body {
  margin: 0;
  padding: 0;
  font-family: "SF UI Display Regular";
  font-size: 20px;
  line-height: 24px;
  font-weight: 400;
  /* min-width: 320px; */ }

a {
  text-decoration: none; }

@font-face {
  font-family: "NewYorkLargeBold";
  src: url("/build/fonts/NewYorkLargeBold.otf");
  font-display: swap; }

@font-face {
  font-family: "SF UI Display Regular";
  src: url("fonts/SFUIDisplay-Regular.woff2"), url("fonts/SFUIDisplay-Regular.woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "SF UI Display bold";
  src: url("fonts/SFUIDisplay-Bold.woff2"), url("fonts/SFUIDisplay-Bold.woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

.header {
  background-color: #ffffff;
  width: 100%; }

.header-container {
  width: 86%;
  min-width: 320px;
  max-width: 768px;
  margin: 0 auto; }

.header-nav {
  border: 1px solid red; }

.header-nav__list {
  margin: 0;
  padding: 0;
  text-align: center;
  margin-bottom: 20px; }

.header-nav__list-item {
  list-style: none;
  padding: 10px 30px; }

.header-user-info {
  display: flex;
  justify-content: center;
  margin-bottom: 20px; }

.header-user-info__sign-in-btn {
  display: inline-block;
  padding: 4px 24px; }

.header-user-info__sign-up-btn {
  display: inline-block;
  padding: 4px 24px;
  color: #ffffff;
  background-color: #FD665E; }

.hamburger-btn {
  cursor: pointer;
  background-color: #ffffff;
  border: none; }

.bar1, .bar2, .bar3 {
  width: 35px;
  height: 4px;
  background-color: #585858;
  margin: 6px 0;
  transition: 0.4s; }

.change .bar1 {
  -webkit-transform: rotate(-45deg) translate(-9px, 6px);
  transform: rotate(-45deg) translate(-9px, 6px); }

.change .bar2 {
  opacity: 0; }

.change .bar3 {
  -webkit-transform: rotate(45deg) translate(-8px, -8px);
  transform: rotate(45deg) translate(-8px, -8px); }

.our-work__title {
  font-family: "Bitstream Vera Serif Bold"; }
