
@font-face {
    font-family: "NewYorkLargeBold";
    src: url("/build/fonts/NewYorkLargeBold.otf");
    font-display: swap;
  }

  @font-face {
    font-family: "SF UI Display Regular";
    src: url("fonts/SFUIDisplay-Regular.woff2"),
      url("fonts/SFUIDisplay-Regular.woff");
    font-weight: 400;
    font-style: normal;
    font-display: swap;
  }

  @font-face {
    font-family: "SF UI Display bold";
    src: url("fonts/SFUIDisplay-Bold.woff2"),
      url("fonts/SFUIDisplay-Bold.woff");
    font-weight: 700;
    font-style: normal;
    font-display: swap;
  }



