.header {
    background-color: #ffffff;
    width: 100%;

}

.header-container{
    width: 86%;
    min-width: 320px;
    max-width: 768px;
    margin: 0 auto;
}

.header-nav {
    border: 1px solid red;
}



.header-nav__list {
    margin: 0;
    padding: 0;
    text-align: center;
    margin-bottom: 20px;

}
.header-nav__list-item{
    list-style: none;
    padding: 10px 30px;
}

.header-search {

}

.header-user-info {

    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}

.header-user-info__sign-in-btn {
    display: inline-block;
    padding: 4px 24px;
}

.header-user-info__sign-up-btn{
    display: inline-block;
    padding: 4px 24px;
    color: #ffffff;
    background-color: #FD665E;
}










//ГАМБУРГЕР

.hamburger-btn {
    cursor: pointer;
    background-color: #ffffff;
    border: none;
}
.bar1, .bar2, .bar3 {
    width: 35px;
    height: 4px;
    background-color: #585858;
    margin: 6px 0;
    transition: 0.4s;
  }

  .change .bar1 {
    -webkit-transform: rotate(-45deg) translate(-9px, 6px);
    transform: rotate(-45deg) translate(-9px, 6px);
  }

  .change .bar2 {opacity: 0;}

  .change .bar3 {
    -webkit-transform: rotate(45deg) translate(-8px, -8px);
    transform: rotate(45deg) translate(-8px, -8px);
  }





  .our-work__title {
    font-family: "Bitstream Vera Serif Bold";
  }
